import React from "react";
import ReactDOM from "react-dom";
import { PolySynth } from "tone";
import { Freeverb } from "tone"

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleUp = this.handleUp.bind(this);
    this.handleDown = this.handleDown.bind(this);
    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
 

    this.noteOne = 73.42;
    this.noteTwo = 110;
    this.noteThree = 146.83;
    this.revDamp = 10
    this.synth = new PolySynth().toMaster();

    this.rev = new Freeverb().toMaster();
    this.synth.connect(this.rev);
    this.rev.dampening.value = this.revDamp;

  }

  handleUp() {
    this.updateSynth(10);
    this.start();
  }

  handleDown() {
    this.updateSynth(-10);
    this.start();
  }

  start() {
    this.synth.triggerAttackRelease([this.noteOne, this.noteTwo, this.noteThree], 1);
  }

  stop() {
    this.synth.triggerAttackRelease([this.noteOne, this.noteTwo, this.noteThree], 1);
  }

  updateSynth(val){
    this.revDamp = this.revDamp + val/10;
    this.rev.dampening.value = this.revDamp;

    this.noteOne = this.noteOne + val;
    this.noteTwo = this.noteTwo + val;
    this.noteThree = this.noteThree + val;
  }



  createTable = () => {
    let table = []

    // Outer loop to create parent
    for (let i = 0; i < 10; i++) {
      let children = []
      //Inner loop to create children
      for (let j = 0; j < 10; j++) {
        children.push(<td key={i + j}>{this.createButtonPair()}</td>)
      }
      //Create the parent and add the children
      table.push(<tr key={i}>{children}</tr>)
    }
    return table
  }

  createButtonPair = () => {
    return(
    <div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
    </div>
    );
  }



  render() {
    
    return (
<div>
      <button onMouseDown={this.start}>
      start
      </button>
     <table>
       <tbody>
       {this.createTable()}
       </tbody>
     </table>
     </div>
      
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));